.textSection svg { max-width: 370px; font-family: 'Aharoni';}
.textSection svg text {
    fill: transparent;
    stroke: #fff;
    stroke-width: 1px;
    stroke-dasharray: 400px;
    stroke-dashoffset: 400px;
	animation: logoAnim 2s linear;
	animation-fill-mode: forwards;
}

.textSection svg#helloText text {
    animation: logoAnim 5s linear;
    animation-fill-mode: forwards;
}
.textSection svg#nameText text {
    animation: logoAnim 4s linear;
    animation-fill-mode: forwards;
}


@keyframes logoAnim {
    from {stroke-dashoffset: 400px;}
    90%{fill: transparent; stroke-width: 1px;}
    to{stroke-dashoffset: 0; fill: #fff; stroke-width: 0;}
}

